@import "scss/";

.wrapper {
  position: fixed;
  top: 0;
  bottom: 0;
  width: $sidebar-width;
  height: 100vh;
  overflow-y: auto;
  opacity: 1;
  transition: all ease-in-out 0.3s;
  &:hover {
    overflow-y: auto;
  }
}

.header {
  height: 4rem;
  display: flex;
  align-items: center;
  justify-content: space-between;
  font-size: 1.25rem;
}

.logo {
  flex: 1;
  display: flex;
  align-items: center;
  justify-content: flex-start;
  padding-left: 1rem;
  // background: linear-gradient(
  //     143.11deg,
  //     #0057ff 8.66%,
  //     #7a5cff 53.84%,
  //     #b61cff 97.17%
  // );
  // background-clip: text;
  // -webkit-background-clip: text;
  // -webkit-text-fill-color: transparent;
  svg {
    fill: #007fff;
  }
}

.links {
  padding: 1rem;
}

.link-item {
  p {
    font-size: 1rem;
    color: #9ca3af;
    text-transform: uppercase;
    font-weight: 600;
    letter-spacing: 1.2;
    margin: 1rem 0;
  }
}

.nav-link {
  display: flex;
  align-items: center;
  padding: 0.75rem 0 0.75rem 0.75rem;
  border-radius: 0.75rem;
  span {
    text-align: center;
    font-weight: 500;
    text-transform: capitalize;
    margin-left: 0.75rem;
    font-size: calc(1rem - 1px);
    padding-top: 4px;
    line-height: 1.25rem;
  }
}


@keyframes fadeIn {
  from {
    opacity: 0;
    transform: translateY(-20px);
  }
  to {
    opacity: 1;
    transform: translateY(0);
  }
}

.fadeInAnimation {
  animation: fadeIn 10.5s ease-in-out;
}

